/* MY styling */

input[type="time"]::-webkit-calendar-picker-indicator {
  background: none;

  /* display: none; */
}

input[type="time"]:focus-visible {
  outline: none;
  border: none;
}

input[type="time"]::-webkit-calendar-picker-indicator {
  background-color: #fff;
  color: #000;
  text-transform: uppercase;
}

.ReactTags__tagInputField {
  background: none;
  border: none;
  width: 100%;
  margin: 12px 6px;
}

.ReactTags__tagInputField:focus-visible {
  border: none;
  outline: none;
}

.tag-wrapper {
  background-color: #bcbcbc;
  color: white;
  border-radius: 6px;
  padding: 4px;
  margin: 4px;
}

.ReactTags__selected {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.ReactTags__remove {
  border: none;
  margin: 4px;
  background: transparent;
  border-radius: 4rem;
  color: black;
  height: 17px;
  width: 17px;
  /* display: flex !important; */
  justify-content: center !important;
  align-items: center !important;
  margin-top: 0px;
}

.time-input {
  border: none;
  background-color: transparent;
  padding-left: 2px;
  padding-right: 2px;
}

.day2_div {
  max-width: fit-content !important;
}

.seconed_main {
  min-width: fit-content;
}

/* My styling */

.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.step-number {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #ccc;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
}

.active .step-number {
  background-color: #007bff;
}

.active .step-title {
  font-weight: bold;
}

.step-title {
  font-size: 14px;
  color: #666;
  text-transform: uppercase;
  font-weight: bold;
}

