.schdule_card {
    width: 20rem;
    background-color: white;
    padding: 20px 10px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
}

.sch_pic {
    width: 100%;
}

.scrolTab {
    width: 100%;
    /* overflow-x: scroll; */
}

.MuiButtonBase-root {
    width: 100%;
    max-width: 400px;
}

.sch_user {
    display: flex;
    align-items: center;
    gap: 5px;

}

.sch_user p {
    margin-bottom: 0px;
    font-size: 16px;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
}

.time_tab_div {
    display: flex;
    align-items: center;
    gap: 5px;
}

.time_tab_div p {
    color: rgba(188, 188, 188, 1);
    font-size: 16px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.main_sch {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;
    align-items: center;
}

.sch_text {
    width: 100%;
    max-width: 380px;
    text-align: center;
}

.inner_schedule_div {
    background-color: white !important;
    margin-top: 90px;
    border-radius: 10px;
    overflow-x: hidden;
}

.schedule_date {
    color: rgba(188, 188, 188, 1);
    font-size: 16px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    text-align: center;
}

.schedule_day {
    color: rgba(22, 24, 41, 1);
    font-size: 19px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    text-align: center;
}

.fill_btn {
    border: 1px solid rgba(45, 156, 219, 1);
    border-radius: 5px;
    background-color: white;
    padding: 5px 0px;
    width: 150px;
    color: rgba(45, 156, 219, 1);
    font-size: 17px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;

}

.inner_schedule_div .table-responsive {
    height: 77vh !important;
}

.platform_btn {
    border: none;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
    padding: 7px 0px;
    width: 150px;
    display: flex;
    justify-content: center;
}

.platform_text {
    color: rgba(22, 24, 41, 1);
    font-size: 14px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.arrow_schedule_div {
    border: 1px solid rgba(151, 151, 151, 1);
    border-radius: 5px;
    width: 30px;
    display: flex;
    justify-content: space-between;
    height: 20px;
    align-items: center;

}

.schedule_date_text {
    color: rgba(188, 188, 188, 1);
    font-size: 14px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.table-responsive::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}

::-webkit-scrollbar-track {
    background: hsl(222, 53%, 93%);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: rgba(188, 188, 188, 1);
    border-radius: 5px;
}

.schudle_grid {
    padding-right: 37px !important;
}

.schedule_arrow {
    color: white;
    font-size: 14px;
    font-weight: 900;
    font-family: 'Inter', sans-serif;
}

.schedule_btn_text {
    color: white;
    font-size: 16px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}
.modal_img_div{
    border: 1px solid rgba(188, 188, 188, 1);
    height: 50px;
    width: 50px;
    border-radius: 50px;
    justify-content: center;
    align-items:center;
    display: flex;
}
.modal_line{
    color: rgba(188, 188, 188, 1);
    font-size: 16px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    text-align: center;
    margin-top: 10px;
}
.modal_day_div{
    background-color: white;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    padding: 3px 0px;
    width: 40px;
    text-align: center;
}
.modal_day_div:hover{
    background-color: rgba(45, 156, 219, 1);
    color: white !important;
}
.modal_day_div:hover .modal_day_div p{
    color: white !important;
}
.modal_day_div p{
    color: rgba(22, 24, 41, 1);
    font-size: 15px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}
.modal_header_text {
    color: rgba(22, 24, 41, 1);
    font-size: 18px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}
.modal_time{
    background-color: white !important;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25) !important;
    border-radius: 5px !important;
    width: 100%;
    max-width: 45%;
    text-align: center;
    padding: 3px 0px;
}
.schedule_modal_cancel{
    color: rgba(188, 188, 188, 1);
    font-size: 18px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    background-color: white;
    border: 1px solid rgba(188, 188, 188, 1);
    border-radius: 10px;
    padding: 10px 0px;
    width: 100%;
    max-width: 40%;
}
.schedule_modal_update{
    color: white;
    font-size: 18px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    background-color: rgba(45, 156, 219, 1);
    border:none;
    border-radius: 10px;
    padding: 10px 0px;
    width: 100%;
    max-width: 40%; 
}
.modal_time p{
    color: rgba(22, 24, 41, 1);
    font-size: 18px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

@media (max-width:1630px) {
    .schudle_grid {
        padding-right: 0px !important;
    }
}

@media (max-width: 990px) {
    .schudle_grid {
        padding-left: 0px !important;
    }
}

@media (max-width:670px) {
    .btndiv_schedule {
        justify-content: center !important;
    }
}

@media (max-width:400px) {
    .schdule_div2 {
        flex-wrap: wrap !important;
        justify-content: center;
    }

    .fill_btn {
        width: 100px;
        font-size: 15px;
    }

    .platform_btn {
        width: 100px;

    }

    .platform_text {
        font-size: 12px;
    }
}

@media (max-width:330px) {
    .schedule_card {
        width: 20rem !important;
    }
}