.payment_card {
    box-shadow: 0px 5px 10px 5px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
    padding: 30px 14px;
}

.payment_card1 {
    box-shadow: 0px 5px 10px 5px rgba(64, 134, 244, 0.5);
    border-radius: 12px;
    padding: 30px 14px;
}

.payment_heading {
    font-weight: 500;
    font-size: 24px;
    color: #161829;
    font-family: 'Inter', sans-serif;
}

.payment_heading2 {
    font-weight: 600;
    font-size: 40px;
    color: #161829;
    font-family: 'Inter', sans-serif;
}

.payment_text {
    font-weight: 400;
    font-size: 18px;
    color: rgba(151, 151, 151, 1);
    font-family: 'Inter', sans-serif;
}

.border_data {
    border-top: 1px solid rgba(188, 188, 188, 1);
    margin-top: 20px;
}

.payment_title {
    font-weight: 400;
    font-size: 18px;
    color: #161829;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;

}

.select_btn {
    background-color: white;
    border: 2px solid rgba(45, 156, 219, 1);
    border-radius: 12px;
    color: rgba(45, 156, 219, 1);
    font-weight: 700;
    font-size: 20px;
    font-family: 'Inter', sans-serif;
    width: 100%;
    max-width: 70%;
    padding: 10px 0px;
}

.payment_mainheading {
    text-align: center;
    font-weight: 700;
    font-size: 22px;
    font-family: 'Inter', sans-serif;
    color: rgba(22, 24, 41, 1);
    margin-top: 20px;

}

.tab_innerwallet {
    background-color: rgba(45, 156, 219, 0.3);
    width: 200px;
    padding: 3px 5px;
    border-radius: 10px;
}

.wallet_nav .nav-link.active,
.nav-pills .show>.nav-link {
    width: 100px;
    background-color: white !important;
    padding: 7px 0px;
    border-radius: 10px;
    color: rgba(22, 24, 41, 1) !important;

}

.wallet_link {
    color: rgba(22, 24, 41, 1) !important;
    text-align: center;
    font-weight: 400 !important;
    font-size: 14px !important;
    font-family: 'Inter', sans-serif;
}

.number_div {
    border: 1px solid rgba(188, 188, 188, 1);
    border-radius: 6px;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F8F8F8;

}

.number_div p {
    color: rgba(22, 24, 41, 1) !important;
    font-weight: 400 !important;
    font-size: 18px !important;
    font-family: 'Inter', sans-serif;
}

.sicial_account {
    color: rgba(22, 24, 41, 1) !important;
    font-weight: 400 !important;
    font-size: 18px !important;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.plan_heading {
    color: rgba(22, 24, 41, 1) !important;
    font-weight: 400 !important;
    font-size: 18px !important;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;

}

.card1 {
    background: #FFFFFF;
    border: 1px solid #BCBCBC;
    box-shadow: 0px 5px 10px 5px #BCBCBC;
    border-radius: 12px;
    height: 620px;
    width: 100%;
    max-width: 45%;

}

.card2 {
    background: #FFFFFF;
    border: 1px solid #2D9CDB;
    box-shadow: 0px 5px 10px 5px rgba(64, 134, 244, 0.5);
    border-radius: 12px;
    height: 620px;
    width: 100%;
    max-width: 45%;

}

.first_input {
    padding-left: 47px !important;
}

.input_check_box {
    border: 1px solid rgba(45, 156, 219, 1) !important;
    border-radius: 0px !important;
    margin-top: 5px;
}

.card_main {
    text-align: center;
    color: rgba(22, 24, 41, 1) !important;
    font-weight: 500 !important;
    font-size: 24px !important;
    font-family: 'Inter', sans-serif;
    margin-top: 20px;
}

.card_main2 {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #161829;

}

.card_main3 {
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #161829;
}

.confirm_text {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #161829;
}

.confirm_textspan {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: rgba(45, 156, 219, 1);
}

@media (max-width:1190px) {
    .card2 {
        height: 700px;
    }

    .card1 {
        height: 700px;
    }
}
@media (max-width:1100px) {
    .card2 {
      max-width: 100%;
    }

    .card1 {
       max-width: 100%;
    }
}
@media (max-width:600px) {
    .payment_heading2 {
        text-align: center !important;
        width: 100% !important;
    }

    .payment_heading {
        text-align: center !important;
        width: 100% !important;
    }

}
@media (max-width:500px) {
    .wallet_container{
        padding-left: 0px !important;
        padding-right: 0px !important;
    }
}
@media (max-width:400px) {
    .payment_flex{
        flex-wrap: wrap !important;
    }
    .payment2{
        width: 100% !important;
        padding: 0px 5px 0px 10px;
    }
    .payment1{
        width: 100% !important;
        padding: 0px 5px 0px 10px;
    }
    .card2{
        height: auto !important;
        padding-bottom: 20px !important;
    }
    .wallet_tab_div{
        margin-bottom: 0px !important;
    }
}
@media (max-width:390px) {
    .payment_heading2 {
        font-size: 30px !important;
    }

    .payment_heading {
        margin-bottom: 0px !important;
    }

    .payment_title {
        font-size: 14px !important;
    }

    .payment_text {
        font-size: 14px !important;

    }
    .card1{
        height: 600px !important;
    }
    .confirm_text {
       font-size: 15px !important;
        
    }
    .confirm_textspan {
        font-size: 15px !important;
    }
}
@media (max-width:325px) {
    .card_main2 {
         font-size: 17px !important;
        
    }
}

@media (max-width:300px) {
    .payment_heading2 {
        font-size: 26px !important;
    }
}