.signin-heading {
  font-weight: bold;
  font-size: 34px;
  text-align: center;
}

.signin-card {
  /* border: 1px solid rgba(0, 0, 0, 0.2); */
  border-radius: 14px;
  font-family: "Inter", sans-serif;
  padding: 14px;
  min-width: 380px;
  width: 30%;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
}

.signin-input {
  padding-left: 8px !important;
  width: 100%;
}

.signin-label {
  align-self: flex-start;
}

@media screen and (max-width: 575px) {
  .signin-card {
    min-width: 270px;
  }
}
