.banner_card {
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    gap: 20px;

}

.banner_heading {
    color: rgba(22, 24, 41, 1);
    font-size: 20px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
}

.banner_name {
    color: rgba(22, 24, 41, 1);
    font-size: 18px;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.banner_mail {
    color: rgba(188, 188, 188, 1);
    font-size: 18px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
}

.banner_text {
    color: rgba(22, 24, 41, 1);
    font-size: 18px;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    margin-bottom: 0px;
    width: 100%;
    max-width: 45%;
}

.call_btn {
    border: none;
    background-color: rgba(45, 156, 219, 1);
    color: white;
    font-size: 16px;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
    padding: 10px;
    width: 120px;
    border-radius: 10px;
}

@media (max-width:1670px) {
    .banner_text {
        max-width: 70% !important;
    }
}

@media (max-width:1400px) {
    .banner_card {
        flex-wrap: wrap !important;
        justify-content: center;

    }

    .banner_user {
        display: flex !important;
        justify-content: center !important;
        width: 100% !important;
    }

    .banner_text {
        text-align: center !important;
        max-width: 100% !important;
    }

    .banner_btn {
        gap: 20px !important;
        justify-content: center !important;
    }

}
@media (max-width:365px) {
    .mobile_row{
        --bs-gutter-x: 0.5rem !important;
    }
}